import React, { useMemo, useRef, useState, useEffect } from 'react';
import { useLoader, useFrame } from '@react-three/fiber';
import { Sphere, Line } from '@react-three/drei';
import * as THREE from 'three';
import InstancedParticles from '../InstancedParticles';
import { latLonToCartesian, calculateDistance, generateArc } from '../utils/geoCalculations';
import InstancedStartRings from '../InstancedStartRings';
import { getFresnelMat } from '../fresnelMat';  // Importiere das Fresnel-Material

const EARTH_RADIUS = 2.2;

function EarthWithParticlesAndClouds({ globePoints, screenSize }) {
  const [textureQuality, setTextureQuality] = useState('4k');
  const [filteredPoints, setFilteredPoints] = useState(globePoints || []);
  const [countryBorders, setCountryBorders] = useState([]);

  const groupRef = useRef();

  useEffect(() => {
    setFilteredPoints(globePoints);
  }, [globePoints]);

  useEffect(() => {
    const [width] = screenSize;
    if (width < 768) {
      setTextureQuality('1k');
    } else if (width < 1200) {
      setTextureQuality('2k');
    } else {
      setTextureQuality('4k');
    }
  }, [screenSize]);

  const [earthTexture, bumpMap, specMap, cloudTexture] = useLoader(THREE.TextureLoader, [
    `/textures/Mapblue_${textureQuality}.png`,
    `/textures/earthbump_${textureQuality}.jpg`,
    `/textures/earthspec_${textureQuality}.jpg`,
    `/textures/fair_clouds_8k.jpg`,  // Cloud texture
  ]);

  useEffect(() => {
    fetch('/ne_110m_admin_0_countries.json')
      .then((response) => response.json())
      .then((data) => {
        const borders = data.features.map((feature) => {
          const { type, coordinates } = feature.geometry || {};
          if (type === 'Polygon') {
            return coordinates.map((ring) =>
              ring.map(([lon, lat]) => latLonToCartesian(lat, lon, EARTH_RADIUS + 0.01))
            );
          } else if (type === 'MultiPolygon') {
            return coordinates.flatMap((polygon) =>
              polygon.map((ring) =>
                ring.map(([lon, lat]) => latLonToCartesian(lat, lon, EARTH_RADIUS + 0.01))
              )
            );
          } else {
            return [];
          }
        });
        setCountryBorders(borders);
      })
      .catch((error) => console.error('Error loading GeoJSON:', error));
  }, []);

  const uniqueStartPoints = useMemo(() => {
    const startPointsSet = new Set();
    const uniquePoints = [];

    globePoints.forEach((point) => {
      const key = `${point.start[0]}-${point.start[1]}`;
      if (!startPointsSet.has(key)) {
        startPointsSet.add(key);
        uniquePoints.push(point.start);
      }
    });

    return uniquePoints;
  }, [globePoints]);

  useFrame(() => {
    if (groupRef.current) {
      groupRef.current.rotation.y += 0.002;
    }
  });

  const handleRingClick = (clickedIndex) => {
    if (clickedIndex !== null) {
      const clickedStartPoint = uniqueStartPoints[clickedIndex];
      const filtered = globePoints.filter((point) => {
        return point.start[0] === clickedStartPoint[0] && point.start[1] === clickedStartPoint[1];
      });

      setFilteredPoints(filtered);
    } else {
      setFilteredPoints(globePoints);
    }
  };

  // Erstelle das Fresnel-Material
  const fresnelMaterial = useMemo(() => getFresnelMat({ rimHex: 0x0088ff, facingHex: 0x000000 }), []);

  return (
    <group ref={groupRef}>
      {/* Erdkugel mit Textur */}
      <Sphere args={[EARTH_RADIUS, 64, 64]} castShadow receiveShadow>
        <meshPhongMaterial
          map={earthTexture}
          bumpMap={bumpMap}
          bumpScale={0.05}
          specularMap={specMap}
          shininess={14}
        />
      </Sphere>

      {/* Wolken-Schicht */}
      <Sphere args={[EARTH_RADIUS + 0.02, 64, 64]} castShadow receiveShadow>
        <meshPhongMaterial
          map={cloudTexture}
          transparent={true}
          opacity={0.1}  // Optional: Opazitt fr Wolken reduzieren
          depthWrite={false}  // Optional: verhindert Tiefe-Schreiben, um transparente Objekte besser darzustellen
        />
      </Sphere>

      {/* Fresnel-Effekt als Overlay auf der Erdkugel */}
      <Sphere args={[EARTH_RADIUS + 0.01, 64, 64]} castShadow receiveShadow>
        <primitive object={fresnelMaterial} />
      </Sphere>

      {filteredPoints.map((point, idx) => (
        <GradientLine
          key={idx}
          start={point.start}
          end={point.end}
          exportValue={point.exportValue}
          offset={0.01 * (idx % 2 === 0 ? 1 : -1)}
        />
      ))}

      <InstancedStartRings uniqueStartPoints={uniqueStartPoints} onRingClick={handleRingClick} />
    </group>
  );
}

function GradientLine({ start, end, exportValue, offset = 0 }) {
  const points = useMemo(() => {
    const startVec = latLonToCartesian(start[0], start[1], EARTH_RADIUS).normalize().multiplyScalar(EARTH_RADIUS);
    const endVec = latLonToCartesian(end[0], end[1], EARTH_RADIUS).normalize().multiplyScalar(EARTH_RADIUS);
    const distance = calculateDistance(start[0], start[1], end[0], end[1]);
    return generateArc(startVec, endVec, distance, 100, offset);
  }, [start, end, offset]);

  return <InstancedParticles points={points} exportValue={exportValue} />;
}

export default EarthWithParticlesAndClouds;