import React, { useMemo, useRef } from 'react';
import * as THREE from 'three';
import { useFrame } from '@react-three/fiber';

function InstancedParticles({ points = [], exportValue, opacity = 0.05 }) {
  const meshRef = useRef();

  // Sicherstellen, dass points immer ein Array ist
  if (!Array.isArray(points)) {
    points = [];
  }

  // Definiere eine einfache lineare Skalierungsfunktion
  function scaleLinear(domainMin, domainMax, rangeMin, rangeMax, value) {
    const scale = (rangeMax - rangeMin) / (domainMax - domainMin);
    return rangeMin + scale * (value - domainMin);
  }

  // Bestimme den minimalen und maximalen Exportwert fr die Skalierung
  const domainMin = Math.min(...points.map(p => p[0])); // Nimm an, dass exportValue von den Punkten abhngt
  const domainMax = Math.max(...points.map(p => p[0]));

  // Fallback, falls keine Punkte vorhanden sind
  const safeDomainMin = domainMin || 0;
  const safeDomainMax = domainMax || 1;

  // Setze den Zielbereich (Range) der Skalierung auf 2 bis 50
  const rangeMin = 2;
  const rangeMax = 80;

  // Wende die Skalierungsfunktion auf den exportValue an
  const particleScale = exportValue
    ? scaleLinear(safeDomainMin, safeDomainMax, rangeMin, rangeMax, Math.min(Math.max(exportValue, safeDomainMin), safeDomainMax))
    : 30;

  // Anzahl der Partikel berechnen
  const numParticles = Math.floor(particleScale);

  // Konvertiere die Punkte in ein flaches Array fr die Verwendung im Shader
  const flatPoints = useMemo(() => {
    if (points.length === 0) {
      return new Float32Array(); // Leeres Array, wenn keine Punkte vorhanden sind
    }

    const flatArray = new Float32Array(points.length * 3); // Platz fr x, y, z-Koordinaten
    points.forEach((point, index) => {
      flatArray[index * 3] = point[0];     // x-Koordinate
      flatArray[index * 3 + 1] = point[1]; // y-Koordinate
      flatArray[index * 3 + 2] = point[2]; // z-Koordinate
    });
    return flatArray;
  }, [points]);

  // Geometrie fr die Partikel erstellen
  const geometry = useMemo(() => {
    const positions = new Float32Array(numParticles * 3);

    // Flle die Positionen basierend auf `flatPoints`
    for (let i = 0; i < numParticles; i++) {
      const index = i % points.length;
      positions[i * 3] = points[index] ? points[index][0] : 0;
      positions[i * 3 + 1] = points[index] ? points[index][1] : 0;
      positions[i * 3 + 2] = points[index] ? points[index][2] : 0;
    }

    const bufferGeometry = new THREE.BufferGeometry();
    bufferGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));

    return bufferGeometry;
  }, [numParticles, flatPoints, points]);

  // ShaderMaterial fr die Partikel (mit nur einer festen Farbe)
  const material = useMemo(() => {
    const uniformColor = new THREE.Color(0x7EB3DB); // Definiere die feste Farbe hier

    return new THREE.ShaderMaterial({
      uniforms: {
        size: { value: 2.5 }, // Partikelgre
        opacity: { value: opacity },
        time: { value: 0 },  // Uniform fr die Zeit
        color: { value: uniformColor }, // Feste Farbe als Uniform
        pointPositions: { value: flatPoints },
      },
      vertexShader: `
        precision mediump float;
        uniform float size;
        uniform float time;
        uniform vec3 pointPositions[${points.length || 1}];
        varying vec3 vColor;

        void main() {
          float progress = mod(time + (float(gl_VertexID) / ${numParticles}.0), 1.0);
          int index = int(floor(progress * float(${points.length || 1})));
          int nextIndex = (index + 1) % ${points.length || 1};

          // Hole aktuelle und nchste Punktposition
          vec3 currentPoint = pointPositions[index];
          vec3 nextPoint = pointPositions[nextIndex];

          // Interpoliere zwischen den Punkten basierend auf dem Fortschritt
          vec3 newPosition = mix(currentPoint, nextPoint, fract(progress * float(${points.length || 1})));

          vColor = vec3(1.0, 1.0, 1.0); // Verwende eine feste weie Farbe, um die Uniformfarbe im Fragment-Shader zu bernehmen
          gl_Position = projectionMatrix * modelViewMatrix * vec4(newPosition, 1.0);
          gl_PointSize = size;
        }
      `,
      fragmentShader: `
        precision mediump float;
        uniform float opacity;
        uniform vec3 color;
        varying vec3 vColor;

        void main() {
          float dist = length(gl_PointCoord - 0.5);
          if (dist > 0.5) discard;
          gl_FragColor = vec4(color, opacity); // Verwende die einheitliche Uniformfarbe hier
        }
      `,
      transparent: true,
      depthTest: true,
      depthWrite: false,
    });
  }, [opacity, flatPoints, points]);

  // Zeitaktualisierung in jedem Frame
  useFrame(({ clock, camera }) => {
    material.uniforms.time.value = clock.getElapsedTime() * 0.06; // Zeituniform aktualisieren 0.01

    // Level of Detail (LOD) basierend auf der Entfernung zur Kamera
    const distance = camera.position.distanceTo(meshRef.current.position);
    if (distance > 50) {
      material.uniforms.size.value = 2; // Weniger Details (kleinere Partikel)
    } else if (distance > 30) {
      material.uniforms.size.value = 3; // Mittlere Detailstufe
    } else {
      material.uniforms.size.value = 4; // Hchste Detailstufe (grere Partikel)
    }
  });

  return <points ref={meshRef} args={[geometry, material]} frustumCulled={false} />;
}

export default InstancedParticles;