// utils/geoCalculations.js

import * as THREE from 'three';
const EARTH_RADIUS = 2.2;

// Funktion zur Berechnung der Entfernung zwischen zwei geografischen Koordinaten (Haversine-Formel)
export function calculateDistance(lat1, lon1, lat2, lon2) {
  const toRadians = (degrees) => degrees * (Math.PI / 180);
  const R = EARTH_RADIUS; // Erdradius in gleichen Einheiten wie der Globus (hier 1)
  const dLat = toRadians(lat2 - lat1);
  const dLon = toRadians(lon2 - lon1);

  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(toRadians(lat1)) * Math.cos(toRadians(lat2)) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);

  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = R * c;
  return distance;
}

// Konvertiere geografische Koordinaten in kartesische
export function latLonToCartesian(lat, lon, radius = EARTH_RADIUS) {
  const phi = (90 - lat) * (Math.PI / 180);
  const theta = (lon + 180) * (Math.PI / 180);
  const x = -(radius * Math.sin(phi) * Math.cos(theta));
  const z = radius * Math.sin(phi) * Math.sin(theta);
  const y = radius * Math.cos(phi);
  return new THREE.Vector3(x, y, z);
}

// Dynamische Berechnung der Segmente und Hhenfaktor basierend auf der Entfernung, mit Verschiebung
export function generateArc(startVec, endVec, distance, maxSegments = 10, offset = 0) {
  const minSegments = 5; // Mindestanzahl von Segmenten
  const segmentCount = Math.max(minSegments, Math.floor(maxSegments * (distance / 5))); // Dynamische Segmentzahl

  const heightFactor = distance * 0.18; // Dynamische Anpassung der Hhe basierend auf der Entfernung

  const arcPoints = [];
  
  // Berechne den Referenz-Offset-Vektor relativ zu einem festen Vektor, z.B. der z-Achse
  const referenceVector = new THREE.Vector3(0, 0, 1); // Feste Richtung entlang der z-Achse
  const offsetDirection = new THREE.Vector3().subVectors(endVec, startVec).normalize().cross(referenceVector).normalize(); 
  // `offsetDirection` wird jetzt eine konstante Richtung haben, relativ zur Verbindungslinie.

  for (let i = 0; i <= segmentCount; i++) {
    const t = i / segmentCount;
    const intermediatePoint = new THREE.Vector3().lerpVectors(startVec, endVec, t);

    // Berechne die Mitte des Bogens und erhhe den Punkt nach auen
    const altitudeFactor = Math.sin(Math.PI * t); // Dies erzeugt die Krmmung des Bogens
    
    // Versetze den hchsten Punkt des Bogens leicht in eine Richtung, um berlappungen zu vermeiden
    const offsetVector = offsetDirection.clone().multiplyScalar(offset * altitudeFactor);
    
    const elevatedPoint = intermediatePoint.clone().normalize().multiplyScalar(EARTH_RADIUS + altitudeFactor * heightFactor).add(offsetVector);

    arcPoints.push(elevatedPoint.toArray());
  }

  return arcPoints;
}