import React, { useEffect, useMemo, useRef } from 'react';
import * as THREE from 'three';
import { useThree } from '@react-three/fiber';
import { latLonToCartesian } from './utils/geoCalculations';  // Importiere latLonToCartesian

const EARTH_RADIUS = 2.2;

function InstancedStartRings({ uniqueStartPoints, onRingClick }) {
  const ringRef = useRef();
  const circleRef = useRef(); // Referenz fr den Klickbereich (unsichtbare Kreise)
  const { raycaster, camera, gl, scene } = useThree(); // Verwende scene, falls ntig
  const numRings = uniqueStartPoints.length;

  const isDragging = useRef(false); // Verwende useRef, um unntige Re-Renders zu vermeiden
  const mouseDownPosition = useRef({ x: 0, y: 0 });

  // Verwende RingGeometry fr den sichtbaren Ring
  const ringGeometry = useMemo(() => new THREE.CircleGeometry(0.01, 16), []);
  // Verwende CircleGeometry fr den unsichtbaren Klickbereich
  const circleGeometry = useMemo(() => new THREE.CircleGeometry(0.05, 32), []); // Erhhe die Kreisgre fr bessere Treffererkennung

  // Erstelle ein InstancedBufferAttribute fr die Farben
  const colors = useMemo(() => {
    const colorArray = new Float32Array(numRings * 3); // Fr RGB
    const color = new THREE.Color();

    // Initialisiere alle Ringe als wei
    for (let i = 0; i < numRings; i++) {
      color.set('white');
      color.toArray(colorArray, i * 3); // Setze Farbe fr jede Instanz
    }

    return colorArray;
  }, [numRings]);

  useEffect(() => {
    if (ringRef.current && circleRef.current) {
      // Setze die Position der Ringe
      uniqueStartPoints.forEach((startPoint, i) => {
        const startVec = latLonToCartesian(startPoint[0], startPoint[1], EARTH_RADIUS + 0.01);
        const matrix = new THREE.Matrix4();
        matrix.setPosition(startVec);
        matrix.lookAt(startVec, new THREE.Vector3(0, 0, 0), new THREE.Vector3(0, 1, 0)); // Ring auf Globusoberflche ausrichten
        ringRef.current.setMatrixAt(i, matrix);
        circleRef.current.setMatrixAt(i, matrix); // Positioniere auch den unsichtbaren Kreis an derselben Stelle
      });

      // Fge das Farben-Attribut hinzu, wenn die Geometrie existiert
      if (ringRef.current.geometry) {
        ringRef.current.geometry.setAttribute('color', new THREE.InstancedBufferAttribute(colors, 3));
        ringRef.current.instanceMatrix.needsUpdate = true; // Stelle sicher, dass die Matrix aktualisiert wird
      }
      if (circleRef.current.instanceMatrix) {
        circleRef.current.instanceMatrix.needsUpdate = true; // Update fr die unsichtbare Kreis-Geometrie
      }
    }
  }, [uniqueStartPoints, colors]);

  useEffect(() => {
    const movementThreshold = 5; // Schwellenwert fr die Bewegung

    const handleMouseDown = (event) => {
      isDragging.current = false; // Setze auf nicht-ziehen beim Drcken
      mouseDownPosition.current = { x: event.clientX, y: event.clientY }; // Speichere die Startposition der Maus
    };

    const handleMouseMove = (event) => {
      const deltaX = Math.abs(event.clientX - mouseDownPosition.current.x);
      const deltaY = Math.abs(event.clientY - mouseDownPosition.current.y);

      if (deltaX > movementThreshold || deltaY > movementThreshold) {
        isDragging.current = true; // Nur setzen, wenn sich die Maus signifikant bewegt hat
      }
    };

    const handleMouseUp = (event) => {
      if (isDragging.current) {
        console.log("Globale Bewegung, Klick ignoriert.");
        return;
      }

      const pointer = new THREE.Vector2();
      pointer.x = (event.clientX / window.innerWidth) * 2 - 1;
      pointer.y = -(event.clientY / window.innerHeight) * 2 + 1;

      raycaster.setFromCamera(pointer, camera);

      // Intersect mit den unsichtbaren Kreisen (nicht den Ringen)
      const intersects = raycaster.intersectObject(circleRef.current, true);

      console.log("Intersected objects: ", intersects);

      if (intersects.length > 0 && intersects[0].instanceId !== undefined) {
        const clickedIndex = intersects[0].instanceId;
        console.log(`Ring ${clickedIndex} clicked!`);

        // Setze alle Ringe auf wei zurck
        const whiteColor = new THREE.Color('white');
        for (let i = 0; i < numRings; i++) {
          whiteColor.toArray(colors, i * 3);
        }

        // Setze den geklickten Ring auf rot
        const redColor = new THREE.Color('#FF6622');
        redColor.toArray(colors, clickedIndex * 3); // Aktualisiere die Farbe auf rot

        // Markiere das Farben-Attribut als aktualisiert
        if (ringRef.current && ringRef.current.geometry && ringRef.current.geometry.attributes.color) {
          ringRef.current.geometry.attributes.color.needsUpdate = true;
        }

        // Rufe den onRingClick-Callback auf und bergebe den geklickten Index
        if (onRingClick) {
          onRingClick(clickedIndex);
        }
      } else {
        console.log("Clicked outside of the rings.");

        // Setze alle Ringe wieder auf wei zurck
        const whiteColor = new THREE.Color('white');
        for (let i = 0; i < numRings; i++) {
          whiteColor.toArray(colors, i * 3);
        }
        if (ringRef.current && ringRef.current.geometry && ringRef.current.geometry.attributes.color) {
          ringRef.current.geometry.attributes.color.needsUpdate = true; // Markiere das Attribut als aktualisiert
        }

        // Wenn auerhalb der Ringe geklickt wird, rufe den onRingClick mit null auf
        if (onRingClick) {
          onRingClick(null); // Klick auerhalb der Ringe
        }
      }
    };

    // Fge Event-Listener fr Mouse-Down, Mouse-Move und Mouse-Up hinzu
    gl.domElement.addEventListener('mousedown', handleMouseDown);
    gl.domElement.addEventListener('mousemove', handleMouseMove);
    gl.domElement.addEventListener('mouseup', handleMouseUp);

    // Cleanup der Eventlistener bei Komponentendemontage oder Neurender
    return () => {
      gl.domElement.removeEventListener('mousedown', handleMouseDown);
      gl.domElement.removeEventListener('mousemove', handleMouseMove);
      gl.domElement.removeEventListener('mouseup', handleMouseUp);
    };
  }, [gl, camera, raycaster, colors, numRings, onRingClick]);

  return (
    <>
      {/* Sichtbare Ringe */}
      <instancedMesh
        ref={ringRef}
        args={[ringGeometry, null, numRings]}
        raycast={THREE.InstancedMesh.prototype.raycast}  // Raycast fr InstancedMesh
      >
        <meshBasicMaterial
          vertexColors={true}
          side={THREE.DoubleSide}
          transparent={true}
          opacity={0.5}
          depthWrite={false}
          depthTest={true}
        />
      </instancedMesh>

      {/* Unsichtbare Kreise fr den Klickbereich */}
      <instancedMesh
        ref={circleRef}
        args={[circleGeometry, null, numRings]}
        raycast={THREE.InstancedMesh.prototype.raycast}  // Raycast fr InstancedMesh
      >
        <meshBasicMaterial visible={false} /> {/* Unsichtbares Material */}
      </instancedMesh>
    </>
  );
}

export default InstancedStartRings;