// hooks/useCountryConnections.js
import Papa from 'papaparse';
import { useEffect, useState } from 'react';

function calculateMaxEntries() {
  const width = window.innerWidth;
  return width > 1920 ? 8000 : width > 1280 ? 7000 : 6000;
}

export function useCountryConnections() {
  const [globePoints, setGlobePoints] = useState([]);
  const [maxEntries, setMaxEntries] = useState(calculateMaxEntries);

  useEffect(() => {
    const handleResize = () => setMaxEntries(calculateMaxEntries());
    window.addEventListener('resize', handleResize);

    Papa.parse('/flows_02.csv', {
      download: true,
      header: true,
      complete: (result) => {
        const data = result.data.filter(row => !isNaN(parseFloat(row.exportValue)))
          .sort((a, b) => parseFloat(b.exportValue) - parseFloat(a.exportValue))
          .slice(0, maxEntries);
        const connections = data.map(row => ({
          start: [parseFloat(row.latOrig), parseFloat(row.lngOrig)],
          end: [parseFloat(row.latExp), parseFloat(row.lngExp)],
          exportValue: parseFloat(row.exportValue),
        }));
        setGlobePoints(connections);
      },
    });

    return () => window.removeEventListener('resize', handleResize);
  }, [maxEntries]);

  return globePoints;
}