import React from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls } from '@react-three/drei';
import './App.css';
import * as THREE from 'three';
import { useCountryConnections } from './hooks/useCountryConnections';
import { useScreenSize } from './hooks/useScreenSize';
import { EffectComposer, Noise, Vignette, DepthOfField } from '@react-three/postprocessing';
import EarthWithParticlesAndClouds from './components/EarthWithParticlesAndClouds';
import DirectionalLightComponent from './components/DirectionalLightComponent';

function App() {
  const globePoints = useCountryConnections();
  const screenSize = useScreenSize();

  return (
    <div className="App">
      <Canvas
        shadows
        style={{ width: '100vw', height: '100vh', backgroundColor: '#00192C' }}
        gl={{ antialias: true }}
        onCreated={({ gl }) => gl.setClearColor(new THREE.Color('#00192C'))}
      >
        <OrbitControls enableZoom={true} />
        <DirectionalLightComponent />
        <ambientLight intensity={10} />

        <EarthWithParticlesAndClouds globePoints={globePoints} screenSize={screenSize} />

        <EffectComposer>
          <DepthOfField focusDistance={0} focalLength={0.01} bokehScale={1} height={600} />
          <Noise opacity={0.01} />
          {/* <Vignette eskil={false} offset={0.1} darkness={0.9} /> */}
        </EffectComposer>
      </Canvas>
    </div>
  );
}

export default App;